import Vuex from 'vuex'
import Vue from 'vue'
import WideBandModule from './wideband'
import UsersModule from './users'

Vue.use(Vuex)

var modules = {
  wideband: WideBandModule,
  users: UsersModule
}

console.log(modules)

export default new Vuex.Store({
  modules
})


