<template>
  <!-- App -->
  <div id="app">

  <div class="panel-overlay"></div>
 
  <!-- Left Panel with Reveal effect -->
  <div class="panel panel-left panel-reveal">
    <div class="content-block">
      <p>Left Panel content here</p>
      <!-- Click on link with "close-panel" class will close panel -->
      <p><a href="#" class="close-panel">Close me</a></p>
      <!-- Click on link with "open-panel" and data-panel="right" attribute will open Right panel -->
      <p><a href="#" data-panel="right" class="open-panel">Open Right Panel</a></p>
    </div>
  </div>

    <!-- Main Views -->
    <f7-views>
      <f7-view id="main-view"  main>
        <f7-pages>
          <navbar title="宽带"></navbar>        
          <f7-page navbar-fixed>
            <div class="tabs subtoolbar-fixed">
              <div class="tab" :class="{active: wideband.bstep == 1}">
                <user-info></user-info>
              </div>
              <div class="tab" :class="{active: wideband.bstep == 2}">
                <serv-pack></serv-pack>
              </div>
              <div class="tab" :class="{active: wideband.bstep == 3}">
                <info-filling></info-filling>
              </div>
              <div class="tab" :class="{active: wideband.bstep == 4}">
                <div class="list-block">
                  
                </div>
              </div>                  
            </div>            
          </f7-page>
        </f7-pages>
      </f7-view>
    </f7-views>
  </div>
</template>

<script>
  import Navbar from './components/Navbar.vue'
  import UserInfo from './components/UserInfo.vue'
  import ServPack from './components/ServPack.vue'
  import InfoFilling from './components/InfoFilling'
  import {mapState} from 'vuex'
  export default {
    computed: {
      ...mapState({
        wideband: state => state.wideband
      })
    },
    components: {
      Navbar,
      UserInfo,
      ServPack,
      InfoFilling
    }
  }
</script>


