<template>
	<div class="user-info">
		<div class="list-block">
			<ul>
			    <li>
			      <div class="item-content">
			        <div class="item-media"><i class="icon icon-form-name"></i></div>
			        <div class="item-inner">
			          <div class="item-title label">客户名</div>
			          <div class="item-input">
			            <input type="text" placeholder="" v-model="data.username">
			          </div>
			        </div>
			      </div>
			    </li>			
			    <li>
			      <div class="item-content">
			        <div class="item-media"><i class="icon icon-form-name"></i></div>
			        <div class="item-inner">
			          <div class="item-title label">证件类型</div>
			          <div class="item-input">
			            <select v-model="data.cerType">
			            	<option>身份证</option>
			            </select>
			          </div>
			        </div>
			      </div>
			    </li>
			    <li>
			      <div class="item-content">
			        <div class="item-media"><i class="icon icon-form-name"></i></div>
			        <div class="item-inner">
			          <div class="item-title label">证件号码</div>
			          <div class="item-input">
			            <input type="text" placeholder="" v-model="data.idenNum">
			          </div>
			        </div>
			      </div>
			    </li>
			    <li>
			      <div class="item-content">
			        <div class="item-media"><i class="icon icon-form-name"></i></div>
			        <div class="item-inner">
			          <div class="item-title label">接入号码</div>
			          <div class="item-input">
			            <input type="text" placeholder="" v-model="data.accessNum">
			          </div>
			        </div>
			      </div>
			    </li>			    
			</ul>
		</div>	
		<div class="content-block">
			<div class="row">
				<!-- <div class="col-50"><a href="#" class="button button-big button-fill color-red"></a></div> -->
				<div class="col-100">
					<a class="button button-big button-fill color-green" @click="ok()">查询</a>
				</div>
			</div>
		</div>
	</div>
</template>

<script type="text/javascript">
	import {mapState} from 'vuex'
	export default {
		data() {
			return {
				data: {}
			}
		},
		mounted() {
			// var that = this
			// this.$store.dispatch('getUser', {
			// 	callback:  (data) => {
			// 		that.data = data
			// 	}
			// })
		},
		methods: {
			ok() {
				var that = this
				this.$store.dispatch('setUser', {
					data: this.data,
					callback: () => {
						that.$emit('change-step', true)
					}
				})
			}
		}
	}
</script>


