<template>
	<div class="info-filling">
		<div class="content-block-title">安装地址</div>
		<div class="list-block">
		  <ul>
		    <!-- Text inputs -->
		    <li>
		      <div class="item-content">
		        <div class="item-media"><i class="icon icon-form-name"></i></div>
		        <div class="item-inner">
		          <div class="item-input">
		            <div class="row">
		            	<div class="col-70">
		            		<input type="text" placeholder="输入安装地址">
		            	</div>
		            	<div class="col-20">
		            		<a href="#" class="button button-fill button-raised">OK</a>
		            	</div>
		            </div>
		          </div>
		        </div>
		      </div>
		    </li>
		  </ul>
		</div>

		<div class="content-block">
			<div class="row">
				<div class="col-50">
					<a class="button button-big button-fill color-red" @click="prev()">上一步</a>
				</div>
				<div class="col-50">
					<a class="button button-big button-fill color-green" @click="next()">下一步</a>
				</div>
			</div>
		</div>

	</div>
</template>

<script type="text/javascript">
	export default {
		methods: {
			prev() {
				this.$emit('change-step', false)
			},
			next() {
				this.$emit('change-step', true)
			}
		}
	}
</script>


