<template>
	<div class="buttons-row business-process-bar">
		<a v-for="(item, index) in list" class="button" :class="{active: item.step <= step}" v-html="item.business"></a>
	</div>
</template>

<style lang="less">
	.business-process-bar {
		.button {
			padding: 0;
			border: none;
			border-radius: 0 !important;
			background: orange;
			color: white;
		}
		.button.active {
			background: #4cd964;
		}
	}
</style>

<script type="text/javascript">
	import BusinessProcess from '../local/BusinessProcess.json'
	export default {
		data() {
			return {
				list: BusinessProcess
			}
		},
		props: ['step']
	}
</script>


