<template>
	<div class="navbar">
		<div class="navbar-inner">
			<div class="left">
				<a v-if="wideband.bstep > 1" class="link" @click="prev()">上一步</a>
			</div>
			<div class="center">
			<span v-html="titleLabel"></span>
			</div>
			<div class="right">
				<a v-if="wideband.bstep != list.length" class="link" @click="next()">下一步</a>
			</div>
			<div class="subnavbar">
				<div class="buttons-row business-process-bar">
					<a v-for="(item, index) in list" class="button" :class="{active: item.step <= wideband.bstep}" v-html="item.business"></a>
				</div>
			</div>
		</div>
	</div>    	
</template>

<style lang="less">
	.business-process-bar {
		.button {
			padding: 0;
			border: none;
			border-radius: 0 !important;
			background: orange;
			color: white;
		}
		.button.active {
			background: #4cd964;
		}
	}
</style>

<script type="text/javascript">
	
	import BusinessProcessBar from './BusinessProcessBar'
	import BusinessProcess from '../local/BusinessProcess.json'
	import {mapState} from 'vuex'
	export default {
		data() {
			return {
				list: BusinessProcess,
				titleLabel: this.title
			}
		},
	    computed: {
	      ...mapState({
	        wideband: state => state.wideband
	      })
	    },
		props: {
			title: {
				type: String,
				default() {
					return '标题'
				}
			}
		},
		methods: {
			prev() {
				this.$store.dispatch('setWidebandStep', {
					dir: false
				})
			},
			next() {
				this.$store.dispatch('setWidebandStep', {
					dir: true
				})
			}
		}
	}

</script>


