export function updateUsersSel({commit}, {user, callback}) {
	commit(types.UPDATE_USERS_SEL, {user})
	callback(user)
}

export function getUsersSel({commit, state}, {callback}) {
	var user = state.userSel
	commit(types.UPDATE_USERS_SEL, {user: {}})
	callback(user)
}


